@font-face {
	font-display: swap;
	font-family: "Inter";
	font-style: normal;
	font-weight: 100 900;
	src: url("../fonts/InterVariable.woff2") format("woff2");
}

@font-face {
	font-display: swap;
	font-family: "Inter";
	font-style: italic;
	font-weight: 100 900;
	src: url("../fonts/InterVariable-Italic.woff2") format("woff2");
}

:root {
    --font: "Inter", sans-serif;
    --bg-html: lch(98% 5 350);
    --bg-hover: lch(20% 35 350);
    --color-border: lch(20% 35 350);
    --color-hover: lch(80% 50 70);
    --color-link: lch(70% 40 350);
    --color-sub: lch(45% 10 350);
    --color-txt: lch(20% 40 350);
    --space-3xs: clamp(0.25rem, 0.2188rem + 0.1563vw, 0.3125rem);
    --space-2xs: clamp(0.5rem, 0.4688rem + 0.1563vw, 0.5625rem);
    --space-xs: clamp(0.75rem, 0.6875rem + 0.3124vw, 0.875rem);
    --space-s: clamp(1rem, 0.9375rem + 0.3124vw, 1.125rem);
    --space-m: clamp(1.5rem, 1.4063rem + 0.4688vw, 1.6875rem);
    --space-l: clamp(2rem, 1.875rem + 0.624vw, 2.25rem);
    --space-xl: clamp(3rem, 2.8125rem + 0.9374vw, 3.375rem);
    --space-2xl: clamp(4rem, 3.75rem + 1.24vw, 4.5rem);
    --space-3xl: clamp(6rem, 5.625rem + 1.874vw, 6.75rem);
    --size-font: clamp(1.25rem, 1.163rem + 0.4348vw, 1.5rem);
}

::selection {
	background: var(--bg-hover);
	color: var(--color-hover);
}

*,
*:after,
*:before {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	background: var(--bg-html);
}

body {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	border: 5px solid var(--color-border);
	color: var(--color-txt);
	display: grid;
		gap: 5px;
		grid-template-rows: 4em minmax(0, 1fr) 4em;
	font-family: var(--font);
		font-feature-settings: 'liga' 1, 'calt' 1, 'cv11' 1, 'ss02' 1, 'ss03' 1, 'zero' 1;
	font-size: var(--size-font);
	font-variant-ligatures: no-common-ligatures;
	line-height: 1.4em;
	position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	overflow: hidden;
}

a {
	color: inherit;
	text-decoration: none;
	transition: all .1s ease-out;
}

a:focus,
a:hover {
	color: var(--color-hover);
	background: var(--bg-hover);
}

h1, h2, h3 {
	font-size: 1em;
}

h1 {
	line-height: 1.4em;
	text-transform: uppercase;
}

.unstyled,
.unstyled:focus,
.unstyled:hover {
	background: none;
	border: none;
	color: unset;
}

#header {
	outline: 5px solid var(--color-border);
	display: grid;
		gap: 5px;
		grid-template-columns: max-content 1fr;
		justify-content: space-between;
	grid-row: 1;
	height: 100%;
	line-height: 0;
	
	& .logo {
		background: var(--bg-hover);
		outline: 5px solid var(--color-border);
		
		&:focus, &:hover {
			cursor: pointer;
		
			& img {
				transform: scale(120%);
			}
		}
		
		& img {
			width: 4em;
			padding: .5em .5em 0 .5em;
			transition: all .1s ease-out;
		}
	}
	
	& .navigation {
		justify-self: right;
		text-transform: uppercase;
		
		& ul {
			display: flex;
			height: 100%;
			
			& li {
				display: flex;
				height: 100%;
				list-style-type: none;
				
				& a {
					font-weight: bold;
					border-left: 5px solid var(--color-border);
					padding: 2em 1em 0 1em;
					
					&:focus, &:hover {
						color: var(--color-hover);
						background: var(--bg-hover);
					}
				}
			}
		}
	}
}

#main {
	display: grid;
		grid-gap: 5px;
		grid-template-columns: 1fr 2fr;
	grid-row: 2;
	
	&.home {
		grid-template-columns: 2fr 1fr;
	}
	
	#content {
		padding: var(--space-l) var(--space-l) var(--space-l) var(--space-l);
		outline: 5px solid var(--color-border);
		overflow-x: none;
		overflow-y: scroll;
	}
	
	#sidebar {
		padding: var(--space-l) var(--space-l) var(--space-l) var(--space-l);
	}
}

#main.list {
	& #content {
		display: flex;
			flex-direction: column;
			gap: 5px;
		padding: 0;
		
		& article {
			outline: 5px solid var(--color-border);
			padding: var(--space-l) var(--space-l) var(--space-l) var(--space-l);
		}
	}
	
	& #sidebar {
		display: flex;
			flex-direction: column;
			justify-content: space-between;
	}
}

#main.home,
#main.single {
	& a {
		border-bottom: 3px solid var(--color-link);
		
		&:focus, &:hover {
			border-bottom: 3px solid var(--color-hover);
		}
	}
	
	& p {
		margin: 0 0 var(--space-s) 0;
		padding: 0;
	}
}

#footer {
	outline: 5px solid var(--color-border);
	grid-row: 3;
	height: 100%;
	padding: 1em 1em 1em 1em;
	
	& .tagline {
		font-style: italic;
	}
}